.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

.button{
    background-color: transparent;
    text-decoration: none;
}

 .hero-no-decoration {
     text-decoration: none;
     color: inherit;
 }

/*===================Desktop====================*/

@media screen and (min-width: 1200px){

    .hero-cont {
        max-width: 1200px;
        padding-left: 32px;
        padding-right: 32px;
        margin: 0 auto;
    }

    .hero-radius{
        border-radius: 30px;
        background: #FCF1E0;
    }

    .hero-font{
        font-family: "Montserrat", sans-serif;
    }

    .hero-backround{
        background: linear-gradient(180deg, rgba(17, 17, 17, 0.00) 0%, rgba(17, 17, 17, 0.40) 100%), url(../images/hero/chocolate-covered-cocoa-powder-1.png), lightgray -70.319px -105.03px / 110.32% 139.265% no-repeat;
        max-width: 1136px;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
    }

    .hero-paragraph-cont{
        padding-top: 381px;
        padding-left: 40px;
        max-width: 484px ;
    }

    .hero-paragraph-font{
        color: #FFF;
        font-size: 28px;
        line-height: 1.21;
        font-weight: 500;
        letter-spacing: -0.84px;
    }

    .hero-buttons-cont{
        display: flex;
        padding-bottom: 40px;
        padding-left: 40px;
        display: flex;
        gap: 14px;
        justify-content: space-between
       
    }

    .hero-button-style{
        display: flex;
        padding: 14px 40px;
        align-items: flex-start;
        border-radius: 100px;
    }

    .hero-button-buy{
        background: #FD9222;
        color: #FFF;
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        border-style: none;
        transition: color, background 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    

    .hero-button-buy:hover{
        background: #FCF1E0;
        color: #FD9222;
    }

    .hero-button-made{
        background: transparent;
        color: #FFF;
        font-size: 14px;
        font-weight: 600;
        line-height: normal;
        border-color: #FFF;
        margin-left: 14px;
        border: 1px solid;
        transition: color, 250ms cubic-bezier(0.4, 0, 0.2, 1);
        text-decoration: none;
    }

    .hero-button-made:hover{
        border: 1px solid #FD9222;
        color: #FD9222;
    }

    .hero-buttons-div{
        display: flex;
        align-items: center;
    }

    .hero-scroll-div{
        display: flex;
        align-items: center;
        margin-right: 40px;
        width: 130px;
        gap: 14px;
    }

    .hero-scroll-div{}


    .hero-button-v{
        border-radius: 50%;
        background-color: #FFF;
        width: 38px;
        height: 38px;
        display: flex;
        align-items: center;
        justify-content: center;
    }


    .hero-button-link{
        color: #FFF;
        font-size: 14px;
        font-weight: 500;
        line-height: 1.28;
        letter-spacing: -0.42px;
    }

}

