    display: none;
  }
  .our-products-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    row-gap: 18px;
    align-items: center;
    margin: 0 auto;
  }

  .our-products-chocolate-container {
    display: block;
    justify-content: center;
    align-items: center;
  }

  .our-products-header {
    color: var(--black-color);
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.36px;
    text-transform: uppercase;
    justify-content: center;
    align-items: center;
    display: flex;
    margin: 8px 0 2px 0;
  }

  .our-products-text {
    color: rgba(17, 17, 17, 0.7);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    letter-spacing: -0.24px;
    justify-content: center;
    align-items: center;
    display: flex;
    margin: 0 0 18px 0;
  }

  .our-products-price {
    border-radius: 100px;
    border: 1px solid rgba(17, 17, 17, 0.1);
    background: var(--white-color);
    width: 94px;
    padding: 8px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    margin: 0 auto;
    color: var(--black-color);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }
}

