        margin: 0 auto;
    }
  
    .top-sellers-image {
       border-radius: 15px;
    background-color: lightgray 50%;
    background-repeat: no-repeat;
    background-size:cover;
    }
  
    .top-sellers-list {
         display: flex;
        gap: 16px;
        margin: 0;
        padding: 0;
    }
  
    .top-sellers-item {
        display: flex;
        justify-content: space-between;
        margin-top: 32px;
        align-items: flex-start;
        gap: 567px;
        flex-shrink: 0;
        }  
    
    .top-sellers-text {
        color:var(--black-color);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 0.28px;
    width: 407px;
    margin:0;
    padding: 0;
    }
    .top-sellers-button {
    background-color:var(--orange-color);
    color:var(--white-color);
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border-radius: 100px;
    border: none;
    padding: 14px 40px;
    gap: 14px;
    }
    .top-sellers-button:hover,
    .top-sellers-button:active {
        background:var(--light-orange);
        color:var(--orange-color);
        transition-property: background-color; 
        transition-duration: 250ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        transition-delay: 0s;
    }
}

